.about {
	section {
		.container {
			width: 750px;
		}		
	}
	#sec1 {
		padding: 70px 0;
		background: linear-gradient(#ffb8ac, #FFF 10%);
		.figure {
			display: block;
			text-align: center;
			margin: 0 auto 70px;
		}
		p {
			font-size: 20px;
		}
	}
	#sec2 {
		.sec-image {
			width: 100%;
			height: auto;
		}
		&:before {
			content: "";
			display: block;
			width: 100%;
			height: 228px;
			background: url(../img/about_sec2_title.png) center repeat-x;
		}
		.container {
			padding: 70px 0;
		}
		h2 {
			font-size: 30px;
			margin-bottom: 50px;
			text-align: center;
		}
		.figures {
			display: flex;
			justify-content: space-between;
		}
		figure {
			flex-basis: 17%;
			text-align: center;
			img {
				margin-bottom: 25px;
				width: 100%;
				height: auto;
			}
			small {
				font-size: 15px;
				display: block;
				margin-bottom: 10px;
			}
			figcaption {
				font-size: 25px;
				color: #4d4d4d;
			}
		}
		hr.spacer {
			border-bottom: 1px solid #444;
			margin: 60px 0;
		}
		.table {
			display: flex;
			flex-wrap: wrap;
			align-items: stretch;
			dt, dd {
				margin-bottom: 40px;
				line-height: 1.5em;
				font-size: 18px;
				color: #4d4d4d;
			}
			dt {
				flex-basis: 22%;
				font-weight: 700;
			}
			dd {
				flex-basis: 76%;
			}
			.head {
				font-weight: 700;
				display: inline-block;
				margin-right: 15px;
			}
			.position {
				font-size: 14px;
			}
			.child {
				flex-wrap: wrap;
				display: flex;
				dt, dd {
					margin-bottom: 0;
				}
				dt {
					flex-basis: 22%;
				}
				dd {
					flex-basis: 77%;
				}
			}
		}
	}
	#sec3 {
		padding: 60px 0;
		background: #ffded8;
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 20px;
			text-align: center;
		}
		p.below {
			font-size: 20px;
			text-align: center;
		}
		.figure {
			margin: 50px 0 100px;
			display: block;
			text-align: center;
		}
	}
	#sec4 {
		padding: 60px 0;
		background: #FFF;
		.group {
			text-align: center;
			@include flex(wrap,space-between,center);
			a.button {
				margin-bottom: 16px;
				border: 3px solid #ff2500;
				font-size: 18px;
				font-weight: 700;
				text-align: center;
				width: 300px;
				height: 50px;
				display: inline-block;
				display: flex;
				align-items: center;
				justify-content: center;
				background: #FFF;
				&:hover {
					background: #ff2500;
					color: #FFF;
				}
			}
		}
	}
}

@media screen and (min-width: 753px) and (max-width: 1024px) {
	.about {
		#sec2 {
			figure {
				flex-basis: 23%;
				margin-bottom: 5%;
			}				
		}	
	}
}

@media screen and (max-width: 768px) {
	.about {
		section {
			.container {
				width: 100%;
			}
		}
		#sec1 {
			padding: 5% 3%;
			.figure {
				margin: 0 auto 5%;
			}
			p {
				font-size: 16px;
			}
		}
		#sec2 {
			.container {
				padding: 5% 3%;
				width: 94%;
			}
			h2 {
				margin-bottom: 5%;
			}
			.figures {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
			}
			figure {
				flex-basis: 48%;
				margin-bottom: 5%;
			}
			hr.spacer {
				margin: 5% 0;
			}
			.table {
				display: flex;
				flex-direction: column;
				dt, dd {
					margin-bottom: 0;
					font-size: 16px;
				}
				dt {
					flex-basis: auto;
				}
				dd {
					flex-basis: auto;
					margin-bottom: 5%;
					font-size: 14px;
				}
				.head {
					display: block;
					margin: 0.5em 0 0;
				}
				.child {
					dt, dd {
						flex-basis: 100%;
					}
				}
			}
		}
		#sec3 {
			padding: 5% 3%;
			h2 {
				font-size: 20px;
			}
			p.below {
				font-size: 16px;
			}
			.figure {
				margin: 3% 0 5%;
			}
			.group a.button {
				font-size: 12px;
				flex-basis: 45%;
				height: 40px;
			}
		}
		#sec4 {
			padding-left: 24px;
			padding-right: 24px;
			.group {
				a {
					display: block;
					&.button {
						width: 100%;
						font-size: 12px;
						height: 40px;
					}
					&:last-of-type {
						margin-bottom: 0;
					}						
				}			
			}	
		}
	}
}