header {
	padding: 20px 0;
	border-bottom: 3px solid #ff2500;
	background: rgba(255, 255, 255, 0.95);	
	&.fixed {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 100;
	}
	.container {
		> ul {
			@include flex(nowrap,space-between,center);
			> li {
				position: relative;
				a {
					font-size: 13px;
					font-weight: 700;
					font-family: inherit;
					display: flex;
					align-items: center;
					&:before {
						content: "";
						/*border:3px solid #ff2500; */
						height: 15px;
						width: 15px;
						box-shadow: 0 0 0 3px #ff2500 inset;
						display: inline-block;
						margin-right: 5px;
					}
					&:hover:before {
						background: #ff2500;
					}
					&.logo {
						padding-right: 2em;
						img {
							width: 90px;
						}
						&:before {
							content: none;
						}
					}
				}
				.sub-nav {
					z-index: 9999;
					position: absolute;
					background-color: rgba(255,255,255,0.95);
					top: 30px;
					left: 0;
					box-sizing: border-box;
					width: 200px;
					padding: 20px;
					visibility: hidden;
					opacity: 0;
					@include animation(.2s,ease-in);
					box-shadow: 0 5px 10px rgba(190,190,190,0.5);
					li {
						margin-bottom: 12px;
						&:last-child {
							margin-bottom: 0;
						}
					}				
				}
				&.toggle-menu {
					&:hover {
						.sub-nav {													
							visibility: visible;
							opacity: 1;
							@include animation(.2s,ease-in);
						}
					}
				}			
			}			
		}
	}
	&.fixed {
		padding: 10px 0;
		z-index: 97;
		width: 100%;
		.container {
			> ul {
				> li {
					a {
						font-size: 12px;
						&:before {
							height: 13px;
							width: 13px;
						}
						&.logo {
							padding-right: 2em;
							img {
								width: 60px;
							}
						}
					}			
				}			
			}
		}
	}
}

.drawer--left {
	.drawer-hamburger {
		display: none;
		right: 0 !important;
		left: auto !important;
		top: -6px;
		z-index: 1002;
		width: 1.7rem !important;
	}	
}
.drawer-hamburger-icon {
	height: 3px !important;
	&:after, &:before {
		height: 3px !important;
	}
}
.drawer-nav {
	background-color: #E6081F !important;
	z-index: 1003 !important;
	aside {
		padding: 3% 0;
	}
	a {
		display: block;
		padding: 0.8em;
		color: #FFF;
		text-decoration: none;
		border-bottom: 1px dotted rgba(255, 255, 255, 0.5);
		font-size: 13px;
		text-align: left;
	}
}

@media only screen and (max-width: 1120px) {

}

@media screen and (max-width: 800px) {
}

@media screen and (max-width: 768px) {
	header {
		padding: 3% 5%;
		width: 90%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 100;
		.container {
			display: none;
		}
		.sp {
			width: 70px;
		}
		&.fixed {
			padding: 3% 5%;
		}
	}
	.drawer--left {
		.drawer-hamburger {
			display: block;
		}		
	}
}