span {
  font-family: inherit;
}

body {
	text-align: center;
	background: #FFF !important;
	@include font-normal();
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-feature-settings: "palt";
	font-feature-settings: "palt";
}
.container {
	width: 980px;
	margin: 0 auto;
	text-align: left;
}
p {
	color: #333;
	font-size: 14px;
	line-height: 1.8em;
	margin: 0 0 1em;
}
li {
	color: #333;
}
a {
	-webkit-transition: all 0.2s ease;
	-moz-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	img {
		-webkit-transition: all 0.2s ease;
		-moz-transition: all 0.2s ease;
		-o-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}
}
input {
	-webkit-transition: all 0.2s ease;
	-moz-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
a img:hover, .btn-small:hover, .btn-apply:hover {
	opacity: 0.9;
}
a {
	color: inherit;
	text-decoration: none;
	font: inherit;
	&:hover {
		text-decoration: none;
	}
}
hr.spacer {
	border: none;
}
.container a img {
	background: rgba(255, 255, 255, 0.001);
}
.sp {
	display: none;
}

/* button */

@media screen and (max-width: 1120px) {
	.container {
		width: auto;
		padding: 0 20px;
		overflow: hidden;
	}
	img {
		max-width: 100%;
		height: auto;
	}
}

@media screen and (max-width: 800px) {
}

@media screen and (max-width: 768px) {
	body {
		padding: 0;
		padding-top: 65px;
	}
	.container {
		width: 100%;
		padding: 0;
	}
	img {
		max-width: 100%;
		height: auto;
	}
	p {
		font-size: 13px;
		line-height: 1.5em;
	}
	.sp {
		display: block;
	}
	.pc {
		display: none;
	}
}