.front {
	#slider {
		.sp-slides {
			width: 100%;
			height: 460px;
			position: relative;
			.slick-arrow {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				display: block;
				font-size: 2rem;
				&.slick-prev {
					left: 20px;
				}
				&.slick-next {
					right: 20px;
				}
			}
			.slick-track {
				position: relative;
				.sp-slide {
					height: 460px;
					position: relative;
					@include background(center,center);
					img,
					svg {
						position: absolute;
						transform: translate(-50%,-50%);
						top: 50%;
						left: 50%;
					}
					a {
						position: absolute;
						display: block;
						width: 100%;
						height: 100%;
					}
				}
			}
			.slick-dots {
				position: absolute;
				width: 100%;
				bottom: 20px;
				@include flex(nowrap,center,center);
				li {
					margin: 4px;
					a {
						display: block;
						box-sizing: border-box;
						border: 2px solid #ff2500;
						width: 15px;
						height: 15px;
						border-radius: 100%;
						text-indent: -999em;
						outline: none;
					}
					&.slick-active {
						a {
							background: #ff2500;
						}
					}
				}
			}
		}
	}
	.news {
		&.sticky {
			border: 1px solid #ff2500;
			border-width: 1px 0 !important;
			position: relative;
			z-index: 2;
			&:before {
				content: "";
				width: 50%;
				height: 100%;
				background: #ff2500;
				left: 0;
				top: 0;
				position: absolute;
				z-index: -1;
			}
		}
		.container {
			display: flex;
			align-items: center;
		}
		.title {
			color: #FFF;
			font-weight: 700;
			text-align: right;
			padding-right: 2%;
		}
		#sticky {
			background: #FFF;
			padding-left: 2%;
			flex-basis: 100%;
			text-align: left;
		}
		.sp-slide {
			min-height: 45px;
			display: flex;
			align-items: center;
			height: 45px !important;
		}
		#sticky {
			a {
				font-size: 12px;
				line-height: 1em;
			}
			.date {
				font-size: 16px;
				display: inline-block;
				margin-right: 15px;
				vertical-align: middle;
			}
			.label {
				font-size: 12px;
				display: inline-block;
				background: #6bb3b3;
				color: #FFF;
				padding: 5px;
				margin-right: 15px;
			}
		}
	}
	#sec1 {
		margin-bottom: 20px;
		.above-sec {
			@include flex(nowrap,space-between,stretch);
			padding-top: 2rem;
			.banners {
				flex-basis: 49%;
				a {
					display: block;
					img {
						display: block;
						width: 100%;
					}
				}
			}
		}
		.sppecial-banner {
			margin-top: 2em;
			img {
				max-width: 100%;
			}
		}
	}
  #sec2,
  #sec-event {
		position: relative;
		.container {
			padding: 15px 0;
			position: relative;
			padding: 15px 0;
			.title {
				display: flex;
				justify-content: space-between;
				margin-bottom: 30px;
				h2 {
					font-size: 17px;
					font-weight: 700;
				}
				a.button {
					border: 3px solid #ff2500;
					font-size: 12px;
					padding: 3px 12px;
					font-weight: 700;
					&:hover {
						background: #ff2500;
						color: #FFF;
					}
				}
			}
		}
		.grids {
			display: flex;
			justify-content: space-between;
			align-items: stretch;
			flex-wrap: wrap;
			li {
				flex-basis: 48%;
				display: flex;
				margin-bottom: 1em;
				.padder {
          flex: 1;
					padding: 0 17px 14px;
				}
				.date {
					font-size: 12px;
					margin-right: 15px;
				}
				.label {
					background: #5986b3;
					color: #FFF;
					font-size: 10px;
          padding: 3px 5px;
          white-space: nowrap;
        }
        img {
          width: 128px;
        }
				h3 {
					font-size: 14px;
					padding-top: 15px;
					line-height: 1.3em;
				}
			}
		}
		.label {
			&.green {
				background: #6bb3b3 !important;
			}
			&.red {
				background: #b34b4b !important;
			}
		}
	}
	#sec3 {
		background: url(../img/front_sec3_bg.jpg) center no-repeat;
		background-size: cover;
		.container {
			display: flex;
			padding: 15px 0;
			justify-content: space-between;
		}
		h2 a {
			background: #ff2500;
			display: flex;
			justify-content: center;
			align-items: center;
			color: #FFF;
			font-size: 20px;
			min-height: 60px;
			height: 60px;
			&:hover {
				background: #FF927F;
			}
		}
		.container .grid {
			flex-basis: 48%;
			padding: 30px 0;
		}
		h3 {
			font-size: 25px;
			text-align: center;
			line-height: 1.4em;
			margin-bottom: 15px;
			height: 60px;
			display: flex;
			align-self: center;
			justify-content: center;
		}
		a.button {
			border: 3px solid #ff2500;
			font-size: 14px;
			padding: 5px 12px;
			font-weight: 700;
			display: block;
			text-align: center;
			&:hover {
				background: #ff2500;
				color: #FFF;
			}
		}
		.figures {
			display: flex;
			justify-content: space-between;
		}
		figure {
			flex-basis: 18%;
			text-align: center;
			img {
				margin-bottom: 25px;
				width: 100%;
				height: auto;
			}
			small {
				font-size: 11px;
				display: block;
				margin-bottom: 10px;
			}
			figcaption {
				font-size: 16px;
			}
		}
		.full-width {
			background: #FFF;
			display: flex;
			justify-content: center;
			align-items: center;
			color: #ff2500;
			font-size: 20px;
			height: 60px;
			width: 100%;
			border-top: 3px solid #ff2500;
			&:hover {
				background: #FF927F;
				color: #FFF;
			}
		}
	}
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
	.front {
		#sec2 {
			.container {
				flex-direction: row;
				padding: 3%;
				width: 94%;
			}
			&:after {
				content: "";
			}
			.container .grid {
				flex-basis: 48%;
			}
		}
	}
}

@media screen and (min-width: 753px) and (max-width: 1024px) {
	.front {
		.news {
			&:before {
				content: block;
			}
			.container {
				flex-direction: row;
			}
			.title {
				color: #FFF;
				font-weight: 700;
				text-align: right;
				padding-right: 2%;
				width: 13%;
			}
			#sticky {
				background: #FFF;
				padding: 0 2%;
				flex-basis: 100%;
				text-align: left;
			}
			.sp-slide {
				min-height: 45px;
				display: flex;
				align-items: center;
				height: 45px !important;
				flex-wrap: nowrap;
			}
			#sticky {
				a {
					font-size: 16px;
					line-height: 1em;
					padding-top: 0 3%;
				}
				.date {
					font-size: 16px;
					display: inline-block;
					margin-right: 15px;
					vertical-align: middle;
					flex-basis: 15%;
				}
				.label {
					font-size: 12px;
					display: inline-block;
					background: #6bb3b3;
					color: #FFF;
					padding: 5px;
					margin-right: 15px;
					text-align: center;
					flex-basis: 13%;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.front {
		margin-top: -9px;
		#slider {
			.sp-slides {
				height: 250px;
				.slick-arrow {
					font-size: 1.4rem;
					&.slick-prev {
						left: 10px;
					}
					&.slick-next {
						right: 10px;
					}
				}
				.slick-track {
					.sp-slide {
						height: 250px;
						img {
							width: 150px;
						}
					}
				}
				.slick-dots {
					bottom: 8px;
					li {
						a {
							width: 12px;
							height: 12px;
						}
					}
				}
			}
		}
		.news {
			&:before {
				content: none;
			}
			.container {
				display: flex;
				align-items: center;
				flex-direction: column;
			}
			.title {
				background: #ff2500;
				padding: 2%;
				width: 96%;
				text-align: center;
			}
			#sticky {
				background: #FFF;
				flex-basis: auto;
				padding: 3%;
				width: 94% !important;
			}
			.sp-slide {
				flex-wrap: wrap;
				height: auto !important;
			}
			#sticky {
				.date {
					font-size: 12px;
				}
				a:not(.label) {
					font-size: 12px;
					display: block;
					margin-top: 5px;
					flex-basis: auto;
				}
			}
		}
		#sec1 {
			.above-sec {
				display: block;
				padding-left: 3%;
				padding-right: 3%;
				.banners {
					margin-bottom: 3%;
					flex-basis: auto;
					&:last-of-type {
						margin-bottom: 0;
					}
				}
			}
			.sppecial-banner {
				padding-left: 3%;
				padding-right: 3%;
				margin-top: 1em;
			}
		}
		#sec3 {
			.container {
				padding: 3%;
				flex-direction: column;
				width: 94%;
				.grid {
					flex-basis: auto;
					padding: 3% 0;
				}
			}
			h3 {
				height: 100%;
			}
			figure figcaption {
				font-size: 14px;
			}
			.full-width {
				background: #FFF;
				display: flex;
				justify-content: center;
				align-items: center;
				color: #ff2500;
				font-size: 20px;
				min-height: 60px;
				width: 100%;
				border-top: 3px solid #ff2500;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.front {
    #sec2,
    #sec-event {
			.container {
				padding: 3%;
				width: 94%;
			}
			.grids li {
				flex-basis: 100%;
			}
		}
	}
}
