.pagination {
	text-align: center;
	margin-bottom: 70px;
	li {
		display: inline-table;
		margin: 0 5px;
		vertical-align: middle;
		a {
			font-size: 12px;
			font-weight: 700;
			color: #FFF;
			text-decoration: underline;
			display: table-cell;
			width: 40px;
			height: 40px;
			background: #ff2500;
			vertical-align: middle;
			border-radius: 4px;
			&:hover {
				color: #FFF;
				background: #666;
			}
		}
		&.onpage a {
			color: #FFF;
			background: #666;
			text-decoration: none;
		}
		&.prev a, &.next a {
			width: auto;
			height: 100%;
			line-height: 1em;
			margin: 0 1em;
			background: none;
			display: inline-block;
			color: #000;
		}
		&.prev a:hover, &.next a:hover {
			color: #000;
			text-decoration: none;
		}
	}
}

@media screen and (max-width: 768px) {
	.pagination {
		width: 100%;
		li {
			display: none;
			&.prev, &.next {
				display: inline-block;
				width: 40%;
				margin: 0 0.5em;
			}
			&.prev a, &.next a {
				display: block;
				width: 100%;
				padding: 1.5em 0;
				background: #ff2500;
				color: #FFF;
				border-radius: 1px;
				margin: 0;
			}
		}
	}
}