.scheme {
	section {
		.container {
			width: 750px;
		}		
	}
	#sec1 {
		padding: 70px 0;
		background: linear-gradient(#FFF 50%, rgba(0, 0, 0, 0.2) 100%);
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 70px;
			text-align: center;
		}
		.grids {
			display: flex;
			justify-content: space-between;
		}
		.grid {
			flex-basis: 56%;
		}
		h3 {
			font-size: 18px;
			font-weight: 700;
			line-height: 1.5em;
			margin-bottom: 50px;
		}
		.grid.slider {
			flex-basis: 40%;
			img {
				width: 100%;
				height: auto;
			}
		}
		.sp-buttons {
			position: relative;
			top: -40px;
			/* margin-bottom: -35px;*/
		}
		.sp-button {
			border-color: #ff2500;
			border-width: 1px;
			width: 8px;
			height: 8px;
		}
		.sp-selected-button {
			background-color: #ff2500;
		}
	}
	#sec2 {
		padding: 70px 0;
		dl {
			display: flex;
			justify-content: space-between;
			align-items: center;
			dt {
				flex-basis: 170px;
			}
		}
		.circle {
			width: 170px;
			height: 170px;
			display: flex;
			flex-direction: column;
			border-radius: 170px;
			align-items: center;
			justify-content: center;
			.above {
				font-size: 10px;
				display: block;
				margin-bottom: 10px;
			}
		}
		h2 {
			font-family: 'Oswald', sans-serif;
			font-size: 22px;
			border: 0px solid #000;
			padding: 7px 0;
			border-width: 1px 0;
			width: 50%;
			text-align: center;
		}
		dl dd {
			flex-basis: 70%;
		}
		h3 {
			font-size: 18px;
			font-weight: 700;
			margin-bottom: 12px;
			line-height: 1.5em;
		}
		p {
			margin-bottom: 0;
		}
		.circle {
			&.blue {
				background: #4da6ff;
			}
			&.gray {
				background: #b3b3b3;
			}
			&.orange {
				background: #ff8b18;
			}
		}
		h3 {
			&.blue {
				color: #4da6ff;
			}
			&.gray {
				color: #b3b3b3;
			}
			&.orange {
				color: #ff8b18;
			}
		}
		.spacer {
			height: 2px;
			margin: 50px 0 30px;
			background: -webkit-linear-gradient(left, #ffffff 0%, #cccccc 50%, #ffffff 100%);
		}
	}
	#sec3 {
		padding: 50px 0;
		background: #e5e5e5;
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 40px;
			text-align: center;
		}
		.figure {
			margin-bottom: 50px;
		}
		h3 {
			font-size: 18px;
			font-weight: 700;
			line-height: 1.5em;
			margin-bottom: 40px;
			color: #4D4D4D;
		}
		p {
			font-size: 18px;
		}
	}
}

@media screen and (min-width: 753px) and (max-width: 1024px) {
	.scheme {
		#sec1 {
			.grids {
				display: flex;
				flex-direction: row;
			}
			.grid {
				flex-basis: 56%;
				&.slider {
					flex-basis: 40%;
				}
			}
		}
		#sec2 {
			dl {
				display: flex;
				flex-direction: row;
				dt {
					flex-basis: 170px;
					margin-bottom: 5%;
				}
				dd {
					flex-basis: 70%;
				}
			}			
		}
	}
}

@media screen and (max-width: 768px) {
	.scheme {
		section {
			.container {
				width: 100%;
			}			
		}
		#sec1 {
			padding: 5% 3%;
			h2 {
				font-size: 17px;
				margin-bottom: 8%;
			}
			.grids {
				display: flex;
				flex-direction: column;
			}
			.grid {
				flex-basis: auto;
			}
			h3 {
				font-size: 16px;
				margin-bottom: 5%;
			}
			.grid.slider {
				flex-basis: auto;
				img {
					width: 100%;
					height: auto;
				}
			}
			.sp-buttons {
				position: static;
				margin-bottom: 0;
			}
		}
		#sec2 {
			padding: 5% 3%;
			dl {
				display: flex;
				flex-direction: column;
				dt {
					flex-basis: auto;
					margin-bottom: 5%;
				}
				dd {
					flex-basis: auto;
					br {
						display: none;
					}
				}
			}
		}
		#sec3 {
			padding: 5% 3%;
			h2 {
				font-size: 20px;
				margin-bottom: 20px;
			}
			.figure {
				margin-bottom: 20px;
			}
			h3 {
				margin-bottom: 20px;
				br {
					display: none;
				}
			}
		}	
	}
}