.partner {
	#sec1 {
		padding: 70px 0;
		h2 {
			font-size: 40px;
			font-weight: 700;
			text-align: center;
			line-height: 1.5em;
			margin-bottom: 40px;
			+p {
				text-align: center;
				margin-bottom: 60px;
				font-size: 20px;
			}
		}
		.grids {
			display: flex;
			flex-wrap: wrap;
			.wrap {
				flex-basis: 18%;
				margin-right: 2%;
				margin-bottom: 2em;
				&:nth-of-type(5n) {
					margin-right: 0;
				}
				figure {
					height: 160px;
					display: flex;
					align-items: center;
					border: 1px solid #CCC;
					box-sizing: border-box;
					justify-content: center;
					margin-bottom: 1em;
					&:hover {
						border-color: #ff2500;
					}
				}
				img {
					max-width: 90%;
					height: auto;
					backface-visibility: hidden;
				}
				.name {
					font-size: 13px;
					font-weight: 700;
					display: block;
					margin-bottom: 0.5em;
					line-height: 1.5em;
				}
				figure.noimage {
					justify-content: center;
					figcaption {
						color: #000;
						font-weight: 700;
						font-family: inherit;
						font-size: 1.1em;
						height: 80px;
						display: flex;
						align-items: center;
						justify-content: center;
						background: #f5f5f5;
						flex-basis: 90%;
						margin: auto;
					}
					+.name {
						display: none;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.partner {
		#sec1 {
			padding: 5% 3%;
			h2 {
				font-size: 20px;
				margin-bottom: 1em;
			}
		}		
	}
}

@media screen and (max-width: 767px) {
	.partner {
		#sec1 {
			.grids {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				.wrap {
					flex-basis: 48%;
					margin-bottom: 1em;
					margin-right: 0;
				}
			}				
		}	
	}
}