aside {
	.anchor {
		height: 1px;
		position: relative;
		/*top: -180px;*/
		&.active {
			top: -60px;
		}
	}
}

.news {
	article {
		padding: 0;
	    section {
			padding: 30px 0;
	    }
		.container {
			width: 750px;
			padding-top: 64px;
			padding-bottom: 64px;
		}
		.page-title {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 50px;
			text-align: center;
			display: block;
		}
	    a {
	    	font-size: 15px;
	    }
		.date {
			font-size: 15px;
			margin-right: 15px;
			vertical-align: middle;
		}
		.label {
			font-size: 10px;
			background: #5986b3;
			color: #FFF;
			padding: 5px;
			margin-right: 15px;
			flex-basis: 10%;
			line-height: 1em;
			text-align: center;
		}
		a:not(.label) {
			flex-basis: 80%;
		}
		h2 {
			font-size: 24px;
			font-weight: 700;
			text-align: center;
			margin-bottom: 2em;
			line-height: 1.4em;
		}
		h3 {
			font-size: 20px;
			font-weight: 700;
			margin: 3em 0 1em;
			line-height: 1.4em;
		}
		blockquote {
			background-color: #EBEBEB;
			padding: 16px;
		}
		p {
			font-size: 16px;
			margin-bottom: 2em;
			line-height: 1.8em;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
		img {
			max-width: 100%;
			height: auto;
		}
		.entry-index {
			border: 1px solid #CCC;
			padding: 1.5em;
			li {
				padding: 0;
				background: none;
			}
			a {
				line-height: 2em;
				display: inline-block;
				margin-top: 0;
				&[href*="-"] {
					text-indent: 1em;
				}
				&:hover {
					text-decoration: underline;
				}
			}
		}
		figure,
		.figures {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			margin: 2em 0;
		}
		figure {
			img {
				margin: auto;
				max-width: 100%;
				height: 100%;
			}
		}
		.figures {
			img {
				max-width: 49%;
				height: 100%;
				margin-bottom: 0.8em;
			}
		}
		.socials {
			margin-top: 36px;
			margin-bottom: 36px;
			height: 18px;
			@include flex(nowrap,center,center);
			a {
				flex-basis: auto;
				border: none;
				display: block;
				margin: 0 16px;
				line-height: 1;
				&:first-of-type {
					margin-left: 0;
				}
				&:last-of-type {
					margin-right: 0;
				}
				.blogicon-bookmark {
					font-size: 36px;
					position: relative;
					top: -3px;
				}
				.ion-social-twitter,
				.ion-social-facebook,
				.ion-social-googleplus,
				.fa-get-pocket {
					font-size: 28px;
				}
			}
		}
	}
	&.archive article {
		.articles {
			border: 1px solid #dbdbdb;
			margin-bottom: 70px;
		}
		li {
			padding: 10px 30px;
			display: flex;
			align-items: center;
			line-height: 1.4em;
			&:nth-of-type(odd) {
				background: #f6f6f6;
			}
		}
	}
	h1.entry-title {
		font-size: 38px;
		font-weight: 700;
		margin: 0.7em 0 1em;
		line-height: 1.5em;
		white-space: -moz-pre-wrap;
		white-space: -pre-wrap;
		white-space: -o-pre-wrap;
		white-space: pre-wrap;
		word-wrap: break-word;
	}
	&.postid-104 {
		article {
			h2 {
			  font-size: 22px;
			}
		}
		#sec1 {
			img {
				margin-bottom: 1em;
			}
			small {
				font-size: 12px;
				display: block;
			}
			.underline {
				text-decoration: underline;
			}
		}
		.report.slider figure {
			position: relative;
			margin: 0 !important;
			display: block;
			img {
				max-width: 100%;
				height: auto;
			}
			figcaption {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				background: rgba(0, 0, 0, 0.8);
				color: #FFF;
				margin: auto;
				padding: 1em;
				font-size: 13px;
			}
		}
		#sec4 {
			.list {
				display: flex;
				flex-wrap: wrap;
				line-height: 1.5em;
			}
			dt, dd {
				box-sizing: border-box;
				font-size: 13px;
			}
			dt {
				flex-basis: 20%;
				font-weight: 700;
			}
			dd {
				flex-basis: 80%;
				border-bottom: none;
				padding: 0;
				margin-bottom: 0.5em;
				padding-left: 1em;
			}
		}
		#sec6 {
			th, td {
				border: 1px solid #CCC;
				padding: 1.5em 1em;
			}
			th {
				width: 13%;
				font-size: 12px;
				background: #f5f5f5;
			}
			td {
				font-size: 13px;
				line-height: 1.7em;
			}
			h3 {
				font-size: 15px;
				font-weight: 700;
				margin-top: 0;
			}
			h4 {
				font-size: 13px;
				font-weight: 700;
				margin: 1em 0 0.5em;
			}
			small {
				font-size: 12px;
				display: block;
				text-indent: 1.1em;
			}
		}
		#sec7 {
			.grids {
				display: flex;
				justify-content: space-between;
				border: 1px solid #CCC;
				border-radius: 3px;
				padding: 1em;
				margin-bottom: 2em;
				figure {
					flex-basis: 21%;
					text-align: center;
					margin: 0;
					img {
						width: 100%;
						height: auto;
					}
				}
				figcaption {
					font-size: 12px;
					font-weight: 700;
					line-height: 1.5em;
					text-align: center;
					margin: auto;
					padding-top: 1em;
				}
				.padder {
					flex-basis: 70%;
					p {
						font-size: 12px;
					}
				}
			}
			.box {
				background: #f5f5f5;
				padding: 1em;
				font-size: 13px;
				line-height: 1.7em;
				.title {
					display: block;
					font-weight: 700;
					margin-bottom: 1em;
				}
			}
		}
		.page-guide {
			display: flex;
			justify-content: center;
			font-size: 12px;
			a {
				margin: 0 2em;
				text-decoration: underline;
			}
		}
	}
}

.postid-280 {
	.grids {
		border: 1px solid #ccc;
		padding: 1em;
		display: flex;
		justify-content: space-between;
		figure {
			margin: 0;
			/*flex-basis: 48%; */
			flex-shrink: 0;
			display: block;
			margin-right: 2em;
			img {
				height: auto;
			}
		}
		.bold {
			display: block;
			/*margin-bottom: 1em;*/
		}
		.padder {
			line-height: 1.5;
		}
	}
}

.single {
	.buttons {
		display: flex;
		justify-content: space-between;
		.button {
			flex-basis: 48%;
			box-sizing: border-box;
		}
	}
}

@media screen and (max-width: 768px) {
	.news {
		article {
			padding: 5% 3%;
			.container {
				width: 100%;
			}
			h2 {
				margin-bottom: 5%;
			}
			.articles {
				margin-bottom: 10%;
			}
			a:not(.label) {
				flex-basis: 100%;
				margin-top: 3%;
			}
			.label {
				flex-basis: 20%;
			}
			li {
				padding: 3%;
				flex-wrap: wrap;
			}
		}
		h1.entry-title {
			font-size: 20px;
		}
		&.archive article {
			li {
				padding: 3%;
				flex-wrap: wrap;
			}
		}
		&.postid-104 {
			.report.slider {
				figure {
					height: auto !important;
					display: block;
				}
			}
			#sec4 {
				dt {
					flex-basis: 100%;
					margin-bottom: 0;
				}
				dd {
					flex-basis: 100%;
					margin-bottom: 1em;
				}
			}
			#sec6 {
				th, td {
					display: block;
					box-sizing: border-box;
					border-width: 1px 1px 0;
				}
				th {
					width: 100%;
					text-align: center;
				}
				tr:nth-last-of-type(1) td {
					border-bottom-width: 1px;
				}
			}
		}
	}
	.postid-280 {
		.grids {
			flex-wrap: wrap;
			figure {
				margin-right: 0;
				width: 100%;
				margin-bottom: 1em;
			}
		}
	}
	.single {
		.buttons {
			flex-wrap: wrap;
			.button {
				flex-basis: 100%;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.news article {
		section {
			padding: 3% 0;
		}
		h2 {
			font-size: 18px;
		}
		h3 {
			font-size: 16px;
		}
		p {
			font-size: 14px;
		}
		.entry-index {
			padding: 0.5em;
			li {
				padding: 0;
			}
			a {
				line-height: 1.4em;
				font-size: 14px;
				margin-top: 2%;
				font-weight: 700;
				&[href*="-"] {
					text-indent: 0em;
					font-size: 13px;
					font-weight: 300;
				}
			}
		}
		figure {
			margin: 1em 0;
		}
		.figures {
			margin: 1em 0;
			img {
				max-width: 100%;
			}
		}
	}
	aside {
		.anchor {
			height: 1px;
			position: relative;
			/*top: -180px;*/
			&.active {
				top: -60px;
			}
		}
	}
}

#report-slider {
  .sp-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0,0,0,0.8);
    color: #FFF;
    font-size: 13px;
  }
}
