.contact {
	section {
		.container {
			width: 750px;
		}		
	}
	#sec1 {
		padding: 70px 0;
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 50px;
			text-align: center;
		}
		background: #FFF;
		padding: 40px;
		margin-bottom: 20px;
		h3 {
			font-size: 24px;
			color: #333;
			margin-bottom: 30px;
			padding: 20px 25px;
			background: #F3F6F8;
			position: relative;
			&.req:after {
				content: "";
				display: inline-block;
				background: url(../img/icon_req.png) no-repeat;
				width: 44px;
				height: 25px;
				position: absolute;
				right: 20px;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
		th.req:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_req.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
		}
		h3.opt:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_opt.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
		}
		th.opt:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_opt.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
			top: 16px;
			margin: 0;
		}
		table {
			width: 100%;
			margin-bottom: 30px;
		}
		th {
			width: 220px;
			vertical-align: top;
			text-align: left;
			color: #333;
			position: relative;
			padding: 10px 0;
			font-size: 16px;
		}
		td {
			padding: 10px 0;
			font-size: 16px;
		}
		th:before {
			content: "";
			display: block;
			width: 100%;
			height: 10px;
		}
		input {
			&[type="text"], &[type="email"], &[type="tel"] {
				width: 97%;
				padding: 1.5%;
				border: 1px solid #CCC;
				border-radius: 4px;
				font-size: 14px;
			}
		}
		textarea {
			width: 97%;
			padding: 1.5%;
			border: 1px solid #CCC;
			border-radius: 4px;
			font-size: 14px;
		}
		.spacer {
			height: 30px;
		}
		a[target="_blank"] {
			color: #333;
			text-decoration: underline;
		}
		input[type="submit"] {
			background: #2475BF url(../img/icon_arrow_double01.png) 18% center no-repeat;
			padding: 25px 0;
			color: #FFF;
			font-size: 16px;
			display: inline-block;
			text-align: center;
			border-radius: 5px;
			width: 500px;
			border: none;
			cursor: pointer;
			&:hover {
				background-color: rgba(36, 117, 191, 0.8);
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.contact {
		section {
			.container {
				width: 100%;
			}				
		}
		#sec1 {
			padding: 8% 3%;
			margin-bottom: 8%;			
			h2 {
				margin-bottom: 8%;
				background: url(../img/icon_title_circle01.png) center 0px no-repeat;
				padding-left: 0;
				padding-top: 20px;
				.sp {
					font-family: serif;
					line-height: 1.4em;
					font-size: 18px;
					font-weight: 700;
				}
			}
			padding: 5%;
			margin-bottom: 20px;
			h3 {
				font-size: 20px;
				margin-bottom: 5%;
				padding: 5%;
			}
			th {
				&.req:after, &.opt:after {
					position: relative;
					left: 5%;
					top: 6px;
				}
			}
			table {
				width: 100%;
				margin-bottom: 5%;
			}
			th {
				width: 100%;
				display: block;
			}
			td {
				display: block;
			}
			th:before {
				content: "";
				display: block;
				width: 100%;
				height: 0;
			}
			.spacer {
				height: 0.5em;
			}
			input[type="submit"] {
				padding: 5% 0;
				font-size: 14px;
				width: 100%;
				background-image: none;
			}
		}
	}
}