.plan {
	section {
		.container {
			width: 800px;
		}		
	}
	#sec1 {
		padding: 70px 0;
		.figure {
			display: block;
			text-align: center;
			margin: 0 auto 80px;
		}
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 50px;
			text-align: center;
		}
		.table {
			display: flex;
			border-bottom: 1px solid #000;
			padding-bottom: 30px;
			margin-bottom: 40px;
			line-height: 1.5em;
			dt {
				flex-basis: 27%;
				padding-right: 30px;
			}
			dd {
				&.middle {
					flex-basis: 55%;
					padding-right: 30px;
				}
				&.last {
					flex-basis: 18%;
					display: flex;
					align-items: center;
				}
			}
			&.head {
				padding-bottom: 5px;
				margin-bottom: 20px;
				font-weight: 700;
			}
			.date {
				display: block;
				font-size: 12px;
				color: #ff2500;
				font-weight: 700;
			}
			.above {
				display: block;
				font-weight: 700;
				font-size: 12px;
			}
			h3 {
				font-size: 15px;
				font-weight: 700;
			}
		}
	}
}

@media screen and (min-width: 753px) and (max-width: 1024px) {
	.plan {
		#sec1 {
			.table {
				flex-direction: row;
				dt {
					flex-basis: 27%;
					padding-right: 30px;
					margin-bottom: 0;
				}
				dd {
					&.middle {
						flex-basis: 55%;
						padding-right: 30px;
						margin-bottom: 0;
					}
					&.last {
						flex-basis: 18%;
						display: flex;
						align-items: center;
					}
				}
				&.head {
					display: flex;
				}
			}				
		}	
	}
}

@media screen and (max-width: 768px) {
	.plan {
		section {
			.container {
				width: 100%;
			}			
		}
		#sec1 {
			padding: 5% 3%;
			.figure {
				margin: 0 auto 8%;
			}
			h2 {
				margin-bottom: 5%;
			}
			.table {
				flex-direction: column;
				dt {
					flex-basis: auto;
					padding-right: 0;
					margin-bottom: 5%;
				}
				dd {
					&.middle {
						flex-basis: auto;
						padding-right: 0;
						margin-bottom: 5%;
					}
					&.last {
						flex-basis: auto;
					}
				}
				&.head {
					display: none;
				}
			}
		}		
	}
}