select::-ms-expand {
  display: none;
}

.company {
	section {
		.container {
			width: 750px;
		}		
	}
	#sec1 {
		padding: 70px 0 0;
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 50px;
			text-align: center;
		}
		.grids {
			display: flex;
			justify-content: space-between;
			margin-bottom: 20px;
		}
		.grid {
			flex-basis: 31%;
			background: #ff5133;
			padding: 5px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			h3 {
				display: flex;
				flex-direction: column;
				align-items: center;
				height: 90px;
				justify-content: center;
				font-size: 17px;
				color: #FFF;
				font-weight: 700;
				.below {
					font-weight: 300;
					margin-top: 10px;
				}
			}
			.row {
				background: #FFF;
				height: 80px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				color: #999;
				text-align: center;
				.above {
					font-size: 14px;
					margin-bottom: 10px;
					display: block;
				}
				.large {
					font-size: 28px;
					font-weight: 700;
				}
			}
		}
		.guide {
			text-align: center;
			font-size: 25px;
		}
	}
	#sec2 {
		padding: 70px 0;
		background: #f1f1f1;
		h2 {
			color: #FFF;
			font-size: 18px;
			font-weight: 800;
			margin-bottom: 15px;
			text-align: center;
			background: #FF2500;
			padding: 10px 0;
			line-height: 1.2em;
			.below {
				display: block;
				font-weight: 400;
			}
		}
		.condition {
			display: flex;
			align-items: stretch;
			color: #FF2500;
			margin-bottom: 15px;
			dt {
				flex-basis: 20%;
				align-self: center;
				font-size: 16px;
				font-weight: 700;
			}
			dd {
				font-size: 16px;
				line-height: 1.4em;
			}
		}
		.price {
			display: flex;
			align-items: flex-end;
			line-height: 1.4em;
			margin-bottom: 30px;
			border: 0px solid #FF2500;
			border-width: 1px 0;
			padding: 40px 0;
			color: #FF2500;
			&:before {
				content: "";
				flex-basis: 290px;
			}
			dt {
				font-size: 16px;
				flex-basis: 180px;
				span:nth-of-type(1) {
					margin-right: 15px;
				}
			}
			dd {
				font-size: 15px;
				text-align: right;
			}
		}
		.grids {
			display: flex;
			justify-content: space-between;
			margin-bottom: 90px;
		}
		section:nth-last-of-type(1) .grids {
			margin-bottom: 0;
		}
		.grid {
			flex-basis: 48%;
			li {
				font-size: 13px;
				text-indent: -1em;
				padding-left: 1em;
				line-height: 1.4em;
			}
		}
		.condition.col1 {
			justify-content: center;
			dt {
				flex-basis: 50px;
			}
		}
		.price.col1 {
			&:before {
				content: none;
			}
			dt {
				flex-basis: 100%;
				text-align: center;
			}
		}
	}
	#sec3 {
		padding: 60px 0;
		text-align: center;
		a.button {
			border: 3px solid #ff2500;
			font-size: 18px;
			font-weight: 700;
			text-align: center;
			width: 300px;
			height: 50px;
			display: inline-block;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #FFF;
			margin: auto;
			&:hover {
				background: #ff2500;
				color: #FFF;
			}
		}
	}
	#sec4 {
		background: #FFF;
		padding: 40px;
		margin-bottom: 20px;
		h2 {
			color: #FF2500;
			font-size: 28px;
			font-weight: 800;
			margin-bottom: 50px;
			text-align: center;
		}
		h3 {
			font-size: 24px;
			color: #333;
			margin-bottom: 30px;
			padding: 20px 25px;
			background: #F3F6F8;
			position: relative;
			&.req:after {
				content: "";
				display: inline-block;
				background: url(../img/icon_req.png) no-repeat;
				width: 44px;
				height: 25px;
				position: absolute;
				right: 20px;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
		th.req:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_req.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
		}
		h3.opt:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_opt.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
		}
		th.opt:after {
			content: "";
			display: inline-block;
			background: url(../img/icon_opt.png) no-repeat;
			width: 44px;
			height: 25px;
			position: absolute;
			right: 20px;
			top: 0;
			bottom: 0;
			margin: auto;
			top: 16px;
			margin: 0;
		}
		h4 {
			font-weight: 700;
		}
		table {
			width: 100%;
			margin-bottom: 30px;
		}
		th {
			width: 220px;
			vertical-align: top;
			text-align: left;
			color: #333;
			position: relative;
			font-size: 15px;
			small {
				display: block;
				font-size: 12px;
				padding-top: 3px;
				text-indent: -0.5em;
			}
			padding: 10px 0;
			font-size: 14px;
		}
		td {
			padding: 10px 0;
			font-size: 14px;
		}
		th:before {
			content: "";
			display: block;
			width: 100%;
			height: 10px;
		}
		input {
			&[type="text"], &[type="email"], &[type="tel"] {
				width: 97%;
				padding: 1.5%;
				border: 1px solid #CCC;
				border-radius: 4px;
				font-size: 14px;
			}
		}
		textarea {
			width: 97%;
			padding: 1.5%;
			border: 1px solid #CCC;
			border-radius: 4px;
			font-size: 14px;
		}
		select {
			width: 45%;
			padding: 10px;
			font-size: 14px;
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			border: 1px solid #CCC;
			background: #FFF;
			background: url(../img/arrow_down_select.png) 90% 50% no-repeat, linear-gradient(to bottom, #fff 0%, #FFF 100%);
			background-size: 10px;
			line-height: 1em;
		}
		.spacer {
			height: 30px;
		}
		a[target="_blank"] {
			color: #333;
			text-decoration: underline;
		}
		input[type="submit"] {
			background: #2475BF url(../img/icon_arrow_double01.png) 18% center no-repeat;
			padding: 25px 0;
			color: #FFF;
			font-size: 16px;
			display: inline-block;
			text-align: center;
			border-radius: 5px;
			width: 500px;
			border: none;
			cursor: pointer;
			&:hover {
				background-color: rgba(36, 117, 191, 0.8);
			}
		}
		.term {
			border: 1px solid #CCC;
			padding: 1em;
			height: 200px;
			overflow: auto;
			margin-bottom: 30px;
			h4 {
				font-weight: 700;
				margin-bottom: 20px;
				font-size: 16px;
			}
			h5 {
				font-weight: 700;
				font-size: 14px;
				margin-bottom: 10px;
			}
			p {
				margin-bottom: 2em;
			}
			ul {
				margin: 0.5em 0 2em;
				padding-left: 0.5em;
			}
			li {
				text-indent: -2em;
				padding-left: 2em;
				font-size: 14px;
				line-height: 1.4em;
				margin-bottom: 10px;
				>ul {
					padding-left: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.company {
		section {
			.container {
				width: 100%;
			}			
		}
		#sec1 {
			padding: 5% 3%;
			.grids {
				margin-bottom: 5%;
				flex-wrap: wrap;
			}
			.grid {
				flex-basis: 45%;
				margin-bottom: 5%;
			}
			.guide {
				font-size: 20px;
			}
		}
		#sec2 {
			padding: 5% 3%;
			.condition {
				display: flex;
				flex-direction: column;
				dt {
					flex-basis: auto;
					margin-bottom: 3%;
				}
			}
			.price {
				flex-direction: column;
				align-items: flex-start;
				&:before {
					content: none;
				}
				dt {
					font-size: 16px;
					flex-basis: auto;
					margin-bottom: 0;
				}
				dd {
					text-align: left;
				}
			}
			.grids {
				flex-direction: column;
				margin-bottom: 10%;
			}
			.grid {
				flex-basis: auto;
			}
			.condition.col1 {
				justify-content: center;
				dt {
					flex-basis: auto;
				}
			}
		}
		#sec3 {
			padding: 5% 3%;
		}
		#sec4 {
			padding: 8% 3%;
			margin-bottom: 8%;
			h2 {
				margin-bottom: 8%;
				background: url(../img/icon_title_circle01.png) center 0px no-repeat;
				padding-left: 0;
				padding-top: 20px;
				.sp {
					font-family: serif;
					line-height: 1.4em;
					font-size: 18px;
					font-weight: 700;
				}
			}
			padding: 5%;
			margin-bottom: 20px;
			h3 {
				font-size: 20px;
				margin-bottom: 5%;
				padding: 5%;
			}
			th {
				&.req:after, &.opt:after {
					position: relative;
					left: 5%;
					top: 6px;
				}
			}
			table {
				width: 100%;
				margin-bottom: 5%;
			}
			th {
				width: 100%;
				display: block;
			}
			td {
				display: block;
			}
			th:before {
				content: "";
				display: block;
				width: 100%;
				height: 0;
			}
			.spacer {
				height: 0.5em;
			}
			input[type="submit"] {
				padding: 5% 0;
				font-size: 14px;
				width: 100%;
				background-image: none;
			}
		}		
	}
}