.booth {
	/* guide
	==================================*/
	
	.guide {
	  padding: 30px 0;
	  background: #f5f5f5;
	  width: 100%;
	  transition: all  0.2s ease;
	  nav {
	    display: flex;
	    justify-content: space-between;
	  }
	  a {
	    font-size: 14px;
	    font-weight: 700;
	    &:hover {
	      color: #FF2500;
	    }
	  }
	  &.fixed {
	    position: fixed;
	    top: 0;
	    left: 0;
	    padding: 1em 0;
	    z-index: 10;
	  }
	}
	
	/* fixed */
	
	@media screen and (max-width: 767px) {
	  .guide {
	    display: none;
	  }
	}
	
	@media screen and (min-width: 768px) and (max-width: 1119px) {
	  .guide {
	    padding: 30px 3%;
	    width: 94%;
	    &.fixed {
	      padding: 1em 3%;
	    }
	  }
	}
	
	/*==================================
	common
	==================================*/
	/* section */
	
	section[id^="sec"] {
	  border-bottom: 1px dashed rgba(0, 0, 0, 0.2);
	  h2 {
	    font-family: 'Noto Sans Japanese', "Meiryo UI" , "" , Meiryo , " " , "MS PGothic" , sans-serif;
	    font-kerning: auto;
	  }
	}
	
	.ready {
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  height: 4em;
	  background: #f5f5f5;
	  color: #AAA;
	  font-family: 'Noto Sans Japanese';
	  font-size: 20px;
	  line-height: 1.3em;
	  font-weight: 100;
	}
	
	@media screen and (max-width: 767px) {
	  section[id^="sec"] h2 br {
	    display: none;
	  }
	  .ready {
	    height: 3em;
	    font-size: 15px;
	    padding: 0.5em;
	  }
	}
	
	/*==================================
	sec1
	==================================*/
	
	#sec1 {
	  padding: 90px 0;
	  h2 {
	    font-size: 40px;
	    font-weight: 700;
	    text-align: center;
	    line-height: 1.5em;
	    margin-bottom: 40px;
	    + p {
	      text-align: center;
	      margin-bottom: 5em;
	      font-size: 20px;
	    }
	  }
	}
	
	@media screen and (max-width: 767px) {
	  #sec1 {
	    padding: 10% 3%;
	    h2 {
	      font-size: 20px;
	      margin-bottom: 1em;
	      + p {
	        font-size: 14px;
	      }
	    }
	  }
	}
	
	/*==================================
	sec2 (SPEAKERS)
	==================================*/
	
	#sec2 {
	  padding: 90px 0;
	  h2 {
	    font-size: 40px;
	    font-weight: 700;
	    text-align: center;
	    line-height: 1.5em;
	    margin-bottom: 40px;
	    + p {
	      text-align: center;
	      margin-bottom: 40px;
	    }
	  }
	  .grids {
	    display: flex;
	    flex-wrap: wrap;
	    align-items: stretch;
	  }
	  .grid {
	    flex-basis: 23%;
	    margin-right: 2.5%;
	    text-align: center;
	    margin-bottom: 50px;
	    &:nth-of-type(4n) {
	      margin-right: 0;
	    }
	    a {
	      outline: none;
	    }
	    img {
	      max-width: 100%;
	      height: auto;
	      margin-bottom: 10px;
	    }
	    .name {
	      display: block;
	      font-size: 16px;
	      font-weight: 700;
	      margin-bottom: 0.5em;
	    }
	    p {
	      font-size: 14px;
	    }
	  }
	  .grids.first {
	    justify-content: center;
	  }
	}
	
	/* grid */
	
	/**/
	
	/*#sec2 .grids.first .grid {margin:0 12% 50px;}*/
	
	/* box */
	
	.lightbox {
	  max-width: 700px;
	  width: 700px;
	  background: #FFF;
	  display: flex;
	  padding: 4%;
	  border-radius: 5px;
	  justify-content: space-between;
	  figure {
	    flex-basis: 30%;
	    img {
	      max-width: 100%;
	      height: auto;
	    }
	  }
	  .padder {
	    display: inline-block;
	    vertical-align: top;
	    flex-basis: 66%;
	  }
	  .name {
	    font-size: 16px;
	    font-weight: 700;
	  }
	  hr {
	    border-bottom: 0px dashed #CCC;
	    border-width: 0 0 1px;
	  }
	  p {
	    font-size: 13px;
	  }
	  .title {
	    font-size: 13px;
	    font-weight: 700;
	    display: block;
	    margin: 1em 0 0;
	  }
	  .wrap-vertical {
	    max-height: 400px;
	    overflow-y: auto;
	  }
	}
	
	.wrap-vertical {
	  &::-webkit-scrollbar {
	    width: 5px;
	    background: #eee;
	    &:vertical {
	      width: 5px;
	      background: #eee;
	    }
	  }
	  &::-webkit-scrollbar-thumb {
	    background: #eee;
	    border-radius: 2px;
	    &:vertical {
	      background: #999;
	      border-radius: 2px;
	    }
	  }
	  &::-webkit-scrollbar-track-piece {
	    &:start, &:end {
	      background: #eee;
	    }
	  }
	  &::-webkit-scrollbar-corner {
	    background: 0 0;
	  }
	}
	
	/**/
	
	#sec2 {
	  .note {
	    background: #f5f5f5;
	    text-align: center;
	    padding: 1em 0;
	  }
	  .off {
	    display: none;
	  }
	}
	
	@media screen and (max-width: 767px) {
	  #sec2 {
	    padding: 10% 3%;
	    h2 {
	      font-size: 20px;
	      margin-bottom: 2em;
	    }
	    .grids {
	      display: flex;
	      justify-content: space-between;
	    }
	    .grid {
	      flex-basis: 46%;
	      margin-right: 0;
	      margin-bottom: 2rem;
	    }
	    .grids.first {
	      justify-content: space-between;
	    }
	  }
	  /* grid */
	  /**/
	  /*#sec2 .grids.first .grid {margin:0 0 2rem;}*/
	  /* box */
	  .lightbox {
	    max-width: 90%;
	    width: 90%;
	    margin: auto;
	    figure {
	      flex-basis: 30%;
	    }
	    .padder {
	      flex-basis: 66%;
	      padding-left: 2%;
	    }
	    .name + p:nth-of-type(1) {
	      font-size: 11px;
	    }
	    p {
	      font-size: 12px;
	    }
	  }
	}
	
	@media screen and (min-width: 768px) and (max-width: 1119px) {
	  #sec2 {
	    padding: 10% 3%;
	  }
	}
	
	/*==================================
	sec3
	==================================*/
	
	#sec3 {
	  padding: 90px 0;
	  h2 {
	    font-size: 40px;
	    font-weight: 700;
	    text-align: center;
	    line-height: 1.5em;
	    margin-bottom: 40px;
	    + p {
	      text-align: center;
	      margin-bottom: 10px;
	      font-size: 20px;
	    }
	  }
	  p.note {
	    font-size: 14px;
	    text-align: center;
	    margin-bottom: 40px;
	  }
	  h3 {
	    font-size: 20px;
	    color: #444;
	    margin-bottom: 1em;
	    line-height: 1.5em;
	    i {
	      margin-right: 0.5em;
	    }
	  }
	  h4 {
	    font-size: 16px;
	    margin-bottom: 1em;
	    font-weight: 700;
	  }
	  .grids {
	    display: flex;
	    flex-wrap: wrap;
	    align-items: stretch;
	  }
	  .grid {
	    flex-basis: 47%;
	    margin-right: 3%;
	    margin-bottom: 2rem;
	    background: #f5f5f5;
	    box-sizing: border-box;
	    padding: 1em;
	    &:nth-of-type(2n) {
	      margin-right: 0;
	    }
	  }
	  .padder {
	    padding: 30px;
	    background: #f5f5f5;
	    height: auto;
	  }
	  h3 {
	    font-size: 20px;
	    margin-bottom: 2em;
	    font-weight: 700;
	  }
	  .padder {
	    small {
	      font-size: 11px;
	    }
	    p {
	      font-size: 13px;
	      margin-bottom: 2em;
	    }
	  }
	  .content p + .title {
	    margin-top: 1em;
	  }
	  .title {
	    font-size: 14px;
	    font-weight: 700;
	    display: block;
	    margin-bottom: 0.5em;
	  }
	  ul.speakers {
	    margin-bottom: 1em;
	    &:nth-last-of-type(1) {
	      margin-bottom: 0;
	    }
	  }
	  .speakers li {
	    font-size: 13px;
	    line-height: 1.7em;
	  }
	}
	
