	/*==================================
	sec4-1
	==================================*/
	
	#sec4-1 {
	  padding: 90px 0;
	  h2 {
	    font-size: 40px;
	    font-weight: 700;
	    text-align: center;
	    line-height: 1.5em;
	    margin-bottom: 40px;
	    + p {
	      text-align: center;
	      margin-bottom: 60px;
	    }
	  }
	  .row {
	    display: flex;
	    justify-content: space-between;
	    border-bottom: 1px dashed #CCC;
	    margin-bottom: 5em;
	    padding-bottom: 3em;
	    dt {
	      flex-basis: 20%;
	      font-size: 20px;
	      font-weight: 700;
	    }
	    dd {
	      &.grids {
	        flex-basis: 78%;
	        display: flex;
	        flex-wrap: wrap;
	        align-items: stretch;
	        .grid {
	          box-sizing: border-box;
	          padding: 2%;
	          /*border:1px solid #e5e5e5;*/
	          margin-bottom: 2em;
	        }
	      }
	      .grid {
	        &:hover {
	          box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2);
	        }
	        a {
	          display: block;
	          width: 100%;
	          height: 100%;
	          img:hover {
	            opacity: 1;
	          }
	        }
	        figure {
	          display: flex;
	          height: 200px;
	          align-items: center;
	          margin-bottom: 10px;
	        }
	        img {
	          width: 100%;
	          height: auto;
	        }
	        .name {
	          font-weight: 700;
	          font-size: 15px;
	        }
	      }
	      .off {
	        display: none;
	      }
	      .grid figure.noimage {
	        justify-content: center;
	        figcaption {
	          color: #000;
	          font-weight: 700;
	          font-family: inherit;
	          font-size: 1.1em;
	        }
	        + .name {
	          display: none;
	        }
	      }
	      &.grids {
	        &.half .grid {
	          flex-basis: 48%;
	          margin-right: 3%;
	          &:nth-of-type(2n) {
	            margin-right: 0;
	          }
	        }
	        &.third {
	          .grid {
	            flex-basis: 31%;
	            margin-right: 3%;
	            &:nth-of-type(3n) {
	              margin-right: 0;
	            }
	          }
	          figure {
	            display: flex;
	            height: 120px;
	            align-items: center;
	            margin-bottom: 10px;
	          }
	        }
	        &.fourth {
	          .grid {
	            flex-basis: 22.7%;
	            margin-right: 3%;
	            &:nth-of-type(4n) {
	              margin-right: 0;
	            }
	          }
	          figure {
	            display: flex;
	            height: 100px;
	            align-items: center;
	            margin-bottom: 10px;
	          }
	        }
	      }
	    }
	  }
	}
	
