	    margin-bottom: 1em;
	    &:hover {
	      border-color: #ff2500;
	    }
	  }
	  img {
	    max-width: 90%;
	    height: auto;
	    backface-visibility: hidden;
	  }
	  .slider {
	    .name {
	      font-size: 13px;
	      font-weight: 700;
	      display: block;
	      margin-bottom: 0.5em;
