html {
	overflow-x:hidden;
	overflow-y:auto;
}

.conference {
	body {
	  text-align: center;
	  &:before {
	    content: none;
	  }
	}

	&.locked {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	#conference-container {
		.container {
		  width: 1120px;
		  margin: 0 auto;
		  text-align: left;
		}
	}
	.end-container {
		text-align: center;
		.end {
			display: inline-block;
			background-color: rgba($red,0.1);
			color: $red;
			margin-bottom: 32px;
			font-size: 1.5rem;
			padding: 24px;
		}
	}

	p {
	  color: #333;
	  font-size: 16px;
	  line-height: 1.8em;
	  margin: 0 0 1em;
	  &:nth-last-of-type(1) {
	    margin-bottom: 0;
	  }
  }

  .comingsoon {
    font-size: 1.6rem;
    text-align: center;
  }

  .comingsoon-wrapper {
    padding: 2em 0;
  }

	li {
	  color: #333;
	}

	a {
	  -webkit-transition: all 0.2s ease;
	  -moz-transition: all 0.2s ease;
	  -o-transition: all 0.2s ease;
	  transition: all  0.2s ease;
	  img {
	    -webkit-transition: all 0.2s ease;
	    -moz-transition: all 0.2s ease;
	    -o-transition: all 0.2s ease;
	    transition: all  0.2s ease;
	  }
	}

	input {
	  -webkit-transition: all 0.2s ease;
	  -moz-transition: all 0.2s ease;
	  -o-transition: all 0.2s ease;
	  transition: all  0.2s ease;
	}

	a img:hover, .btn-small:hover, .btn-apply:hover {
	  opacity: 0.9;
	}

	a {
	  color: inherit;
	  text-decoration: none;
	  font: inherit;
	  &:hover {
	    text-decoration: none;
	  }
	}

	hr.spacer {
	  border: none;
	  height: 30px;
	}

	.container a img {
	  background: rgba(255, 255, 255, 0.001);
	}

	.sp {
	  display: none;
	}

	.themes {
		margin-bottom: 24px;
		@include flex(wrap,space-between,stretch);
		.theme {
			text-align: left;
			flex-basis: 48%;
			margin-bottom: 32px;
			h4 {
				font-weight: 600;
				margin-bottom: 16px;
			}
			p {
				font-size: 13px;
				text-align: left !important;
			}
		}
	}

	@media screen and (max-width: 767px) {
	  body {
	    padding: 0;
	  }
	  #conference-container {
	  .container {
	    width: 100%;
	    padding: 0;
	  }
	  .hero {
		 img {
			 &.sp {
				 width: 90%;
			 }
		 }
	  }
	  }
	  img {
	    max-width: 100%;
	    height: auto;
	  }
	  p {
	    font-size: 14px;
	    line-height: 1.5em;
	  }
	  .sp {
	    display: block;
	  }
	  .pc {
	    display: none;
	  }
	.themes {
		margin-bottom: 24px;
		display: block;
		align-items: inherit;
		.theme {
			text-align: left;
			flex-basis: auto;
			margin-bottom: 32px;
			h4 {
				font-weight: 600;
				margin-bottom: 16px;
			}
			p {
				font-size: 13px;
				text-align: left !important;
			}
		}
	}
	}

	@media screen and (min-width: 768px) and (max-width: 1023px) {
	  .container {
	    width: 100%;
	    padding: 0;
	  }
	  img {
	    max-width: 100%;
	    height: auto;
	  }
	}

	@media screen and (min-width: 1024px) and (max-width: 1119px) {
	  .container {
		box-sizing: border-box;
	    width: 96%;
	    padding: 0 2%;
	    zoom: 0.9;
	  }
	  img {
	    max-width: 100%;
	    height: auto;
	  }
	}

	// header

	body {
	  &.conference2017, &.booth {
	    padding-top: 78px;
	  }
	}

	@media screen and (max-width: 770px) {
	  body {
	    &.conference2017, &.booth {
	      padding-top: 70.59px;
	    }
	  }
	}

	@media screen and (min-width: 770px) {
	  .drawer--left .drawer-hamburger {
	    display: none;
	  }
	}

	// nav

	header.fixed {
	  z-index: 97;
	  width: 100%;
	}

	.drawer--left .drawer-hamburger {
	  right: 0 !important;
	  left: auto !important;
	  top: -2px;
	  z-index: 1002;
	  width: 1.7rem !important;
	}

	.drawer-hamburger-icon {
	  height: 4px !important;
	  &:after, &:before {
	    height: 4px !important;
	  }
	}

	.drawer-nav {
	  background-color: #E6081F !important;
	  z-index: 1003 !important;
	  aside {
	    padding: 3% 0;
	  }
	  a {
	    display: block;
	    padding: 0.8em;
	    color: #FFF;
	    text-decoration: none;
	    border-bottom: 1px dotted rgba(255, 255, 255, 0.5);
	    font-size: 13px;
	    text-align: left;
	  }
	}


	// OPTIONAL

	.wrap {
	  &::-webkit-scrollbar {
	    width: 5px;
	    background: #eee;
	    &:horizontal {
	      width: 5px;
	      background: #eee;
	    }
	  }
	  &::-webkit-scrollbar-thumb {
	    background: #eee;
	    border-radius: 2px;
	    &:horizontal {
	      background: #999;
	      border-radius: 2px;
	    }
	  }
	  &::-webkit-scrollbar-track-piece {
	    &:start, &:end {
	      background: #eee;
	    }
	  }
	  &::-webkit-scrollbar-corner {
	    background: 0 0;
	  }
	}

	// shadow

	.shadow {
	  box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.2);
	}

	// size

	.full-width {
	  width: 100%;
	}

	// text

	.center {
	  text-align: center !important;
	}

	.bold {
	  font-weight: bold;
	}

	.underline {
	  text-decoration: underline;
	}

	.red {
	  color: #d62a16;
	}

	.yellow {
	  color: #FFFF00;
	}

	// font size

	.q18 {
	  font-size: 18px !important;
	}


	//hero

	.hero,
	.hero2018 {
	  @include background(center,center);
	  height: 500px;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	}


	.hero-image {
		background: none;
		img {
			max-width: 100%;
		}
			.sp {
				display: none;
			}
		@media screen and (max-width: 768px) {
			margin-top: -9px;
			.sp {
				display: block;
			}
			.pc {
				display: none;
			}
		}
	}

	.hero2018_sp {
		display: none;
	}

	@media screen and (max-width: 768px) {
	  .hero {
		margin-top: -9px;
	    height: 200px;
	    padding: 3%;
	    overflow: hidden;
	  }
	  .hero2018 {
		display: none;
	  }
	  .hero2018_sp {
	    display: block;
	    margin-top: -9px;
		width: 100%;
	  }
	}

	@media screen and (min-width: 768px) and (max-width: 1119px) {}

	// guide

	.guide {
	  padding: 30px 0;
	  background: #f5f5f5;
	  box-sizing: border-box;
	  @include animation(.2s,ease-in);
	  nav {
	    @include flex(nowrap,space-around,center);
	  }
	  a {
	    font-size: 14px;
	    font-weight: 700;
	    &:hover {
	      color: #FF2500;
	    }
	  }
	  &.fixed {
		@include animation(.2s,ease-in);
		width: 100%;
	    position: fixed;
	    top: 0;
	    left: 0;
	    padding: 1em 0;
	    z-index: 10;
	  }
	}

	// fixed

	@media screen and (max-width: 767px) {
	  .guide {
	    display: none;
	  }
	}

	@media screen and (min-width: 768px) and (max-width: 1119px) {
	  .guide {
	    padding: 30px 3%;
	    &.fixed {
	      padding: 1em 3%;
	    }
	  }
	}

	// common
	// section

	section[id^="sec"] {
	  border-bottom: 1px dashed rgba(0, 0, 0, 0.2);
	}

	.ready {
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  height: 4em;
	  background: #f5f5f5;
	  color: #AAA;
	  font-size: 20px;
	  line-height: 1.3em;
	  font-weight: 100;
	}

	/* button */

	.buttons {
	  margin: 3em 0;
	}

	a.button {
	  background: #FF2500;
	  padding: 25px 0;
	  color: #FFF;
	  font-size: 16px;
	  display: inline-block;
	  text-align: center;
	  border-radius: 5px;
	  width: 300px;
	  border: none;
	  cursor: pointer;
	  &:hover {
	    background-color: rgba(36, 117, 191, 0.8);
	  }
	  background: #FF2500;
	  padding: 25px 0;
	  color: #FFF;
	  font-size: 16px;
	  display: inline-block;
	  text-align: center;
	  border-radius: 5px;
	  width: 300px;
	  border: none;
	  cursor: pointer;
	}

	@media screen and (max-width: 767px) {
	  section[id^="sec"] h2 br {
	    display: none;
	  }
	  .ready {
	    height: 3em;
	    font-size: 15px;
	    padding: 0.5em;
	  }
	  a.button {
	    font-size: 14px;
	    width: 100%;
	  }
	}

	.above-sec {
	  padding-top: 2rem;
	}

