	// sec2 (SPEAKERS)

	#sec2 {
	  padding: 90px 0;
	  h2 {
	    font-size: 40px;
	    font-weight: 700;
	    text-align: center;
	    line-height: 1.5em;
	    margin-bottom: 40px;
	    + p {
	      text-align: center;
	      margin-bottom: 40px;
	    }
	  }
	  .grids {
	    display: flex;
	    flex-wrap: wrap;
	    align-items: stretch;
	    justify-content: center;
	  }
	  .grid {
	    flex-basis: 23%;
	    margin-right: 2.5%;
	    text-align: center;
	    margin-bottom: 50px;
	    &:nth-of-type(4n) {
	      margin-right: 0;
	    }
	    a {
	      outline: none;
	    }
	    img {
	      max-width: 100%;
	      height: auto;
	      margin-bottom: 10px;
	    }
	    .name {
	      display: block;
	      font-size: 16px;
	      font-weight: 500;
	      margin-bottom: 0.5em;
	    }
	    p {
		  font-weight: 300;
	      font-size: 12px;
	    }
	  }
	  .grids.first {
	    justify-content: center;
	  }
	}

	.lightbox {
	  max-width: 700px;
	  width: 700px;
	  background: #FFF;
	  display: flex;
	  padding: 4%;
	  border-radius: 5px;
	  justify-content: space-between;
	  figure {
	    flex-basis: 30%;
	    img {
	      max-width: 100%;
	      height: auto;
	    }
	  }
	  .padder {
	    display: inline-block;
	    vertical-align: top;
	    flex-basis: 66%;
	    hr {
			border-bottom: 0px dashed #CCC;
			border-width: 0 0 1px;
		}
		.name {
			font-size: 16px;
		    font-weight: 500;
		    display: block;
		    line-height: 1;
		    margin-bottom: 12px;
		}
	    p {
		  font-weight: 300;
		  font-size: 12px;
		  &.history {
			  font-size: 11px;
		  }
	    }
	  }
	  .title {
	    font-size: 13px;
	    font-weight: 700;
	    display: block;
	    margin: 1em 0 0;
	  }
	  .wrap-vertical {
	    max-height: 400px;
	    overflow-y: auto;
	  }
	}

	.wrap-vertical {
	  &::-webkit-scrollbar {
	    width: 5px;
	    background: #eee;
	    &:vertical {
	      width: 5px;
	      background: #eee;
	    }
	  }
	  &::-webkit-scrollbar-thumb {
	    background: #eee;
	    border-radius: 2px;
	    &:vertical {
