				}
				p {
					&.history {
						text-align: center;
					}
					&.profile {
						font-size: 11px;
						line-height: 1.8;
						letter-spacing: 0.6px;
					}
				}
			}
		}
	}

	@media screen and (min-width: 768px) and (max-width: 1119px) {
	  #sec2 {
	    padding: 10% 3%;
	  }
	}

