	          height: auto;
	        }
	        .name {
	          font-weight: 700;
	          font-size: 15px;
	          line-height: 1.7;
	        }
	      }
	      .off {
	        display: none;
	      }
	      .grid figure.noimage {
	        justify-content: center;
	        figcaption {
	          color: #000;
	          font-weight: 700;
	          font-size: 1.1em;
	        }
	        + .name {
	          display: none;
	        }
	      }
	      &.grids {
	        &.half .grid {
	          flex-basis: 48%;
	          margin-right: 3%;
	          &:nth-of-type(2n) {
	            margin-right: 0;
	          }
	        }
	        &.third {
	          .grid {
	            flex-basis: 31%;
	            margin-right: 3%;
	            &:nth-of-type(3n) {
	              margin-right: 0;
	            }
	          }
	          figure {
	            display: flex;
	            height: 120px;
	            align-items: center;
	            margin-bottom: 10px;
	          }
	        }
	        &.fourth {
	          .grid {
	            flex-basis: 22.7%;
	            margin-right: 3%;
	            &:nth-of-type(4n) {
	              margin-right: 0;
	            }
	          }
	          figure {
	            display: flex;
	            height: 100px;
	            align-items: center;
	            margin-bottom: 10px;
	          }
	        }
	      }
	    }
	  }
	}

